#ThreeJsCanvas {
  background-color: #000;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
}

#steeringWheelContainer {
  z-index: 100;
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
  position: fixed;
  bottom: -25%;
  left: 50%;
  overflow: hidden;
  transform: translateX(-50%)translateY(50%);
}

#steeringWheel {
  width: 100%;
  height: auto;
  transition: transform .1s;
  position: absolute;
  bottom: -50%;
}

.welcome-message {
  z-index: 1000;
  backdrop-filter: blur(15px);
  color: #ffffffb5;
  text-shadow: 0 2px 4px #0000004d;
  mix-blend-mode: hard-light;
  background-color: #789c;
  border-radius: 4px;
  padding: 30px;
  font-family: Courier New, monospace;
  font-size: 18px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 4px 8px #0003, 0 6px 20px #004d4066;
}

button {
  color: #f5f5f5;
  cursor: pointer;
  background-color: #006400b3;
  border: 1px solid #4b5320;
  border-radius: 2px;
  margin: 20px auto;
  padding: 10px 20px;
  font-family: Courier New, monospace;
  font-size: 16px;
  transition: all .3s;
  display: block;
  box-shadow: 0 2px 4px #0003, inset 0 1px 2px #ffffff4d;
}

button:hover {
  background-color: #008000cc;
  transform: translateY(-2px);
  box-shadow: 0 0 12px #008000cc;
}
